import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid'
import Event from './Event'
import LazyLoad from 'react-lazy-load'
import {Link} from 'react-router-dom'

import Valentines from './static/events/valentines.jpg'
import Scarlett from './static/events/scarlett.jpg'
import Northlands from './static/events/northlands.jpg'
import Woodlands from './static/events/woodlands.jpg'
import Hariss from './static/events/hariss.jpg'
import United from './static/events/united.jpg'
import Castledon from './static/events/castledon.jpg'
import Reece from './static/events/reece.jpg'
import PoliceWay from './static/events/policeway.jpg'

import Skydive from './static/events/skydive.jpg'

import Sen from './static/events/sen.jpg'
import Eighties from './static/events/80s.jpg'

import JJ from './static/events/jj.jpg'
const useStyles = makeStyles(theme =>({

}))
function Events() {
    const classes = useStyles();
    const placeholder = "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vestibulum porta, dui eleifend vehicula gravida, ante orci tristique urna, non varius metus ligula id metus. Integer a metus velit. Vivamus in pulvinar elit. Aliquam eget pretium lorem, sit amet tincidunt ante. Nulla finibus lorem vel elit vehicula vulputate. Mauris tempus lectus in dui ultrices, nec maximus lorem posuere. Suspendisse posuere, dui vitae placerat imperdiet, orci nibh consequat magna, vel lacinia urna lorem a eros. Aenean vel facilisis augue. Nam ut volutpat nibh. Donec volutpat magna sit amet aliquam maximus. Nam et sem sit amet quam volutpat hendrerit et vel nisi. Praesent ullamcorper lacus eu quam dictum tincidunt. Nunc elit ipsum, aliquam nec pellentesque id, placerat eu quam."
  return (
    <div className={classes.root}>
        <Grid 
        container
        spacing={0}
        alignItems="center"
        justify="center"
        >
            <Grid item xs={10}>
                <LazyLoad offsetVertical={300}>
                    <Event
                        title="The Not Valentines Ball"
                        date="8th Febuary 2020"
                        image={Valentines}
                        text={<div>We held a ball to allow all of our supporters to get glammed up and enjoy some great food and entertainment. The charities this fundraiser supported were <a href="https://stlukeshospice.com/">St. Lukes Hospice</a> and autism charity <a href="https://www.annakennedyonline.com/">AnnaKennedyOnline</a>. We were entertained by the amazing group Abacus and singers David Smith and Mike Barnard. Dawn Avery read a poem written on autism and Aston gave a very moving speech. The night saw us raise an amazing £3001, split equally between the three charities. We were also joined by the lovely Vicky Michelle!</div>}
                        direction="row"
                    />
                </LazyLoad>
                <LazyLoad offsetVertical={300}>
                    <Event 
                        title="Scarletts Ball"
                        date="???"
                        image={Scarlett}
                        text={<div>Scarlett's Ball was a suprise for many people, including us! The ball was arranged and put together by our wonderful friend Lynne Mohseni and her friends, plus a few team members. The aim was to raise funds for <Link to="/pamper">Aston's Pamper Project</Link> as a surprise event for us! We had no idea that it was happening until the night! Even the name "Scarlett Kipper" was a fake name she'd set up as a cover. Unti we got there, we had no idea it had all been set up by Lynne! We can't thank her and her friends enough for such a wonderful night, and raising £3000 for Aston's Pamper Project.</div>}
                        direction="row-reverse"
                    />
                </LazyLoad>
                <LazyLoad offsetVertical={300}>
                    <Event 
                        title="This is Me - Northlands School"
                        date="???"
                        image={Northlands}
                        text={<div>Aston and his parents went into a local school and met up with some young parents. He treated them to a short chat about himself and how he's overcome so much. He wanted to give some hope to the parents that are unclear of their future parenting children with Special Educational Needs and give them a reason to see the positives. It was a hugely successful day and many new friendships were formed.</div>}
                        direction="row"
                    />
                </LazyLoad>
                <LazyLoad offsetVertical={300}>
                    <Event 
                        title="Aston's Way - Woodlands School"
                        date="???"
                        image={Woodlands}
                        text={<div><Link to="/astons-way">Aston's Way</Link> (in collaboration with Training 2 Care) saw an Autism Reality bus taken into a local senior school, Woodlands. A number of staff were trained in sensory issues by going through the experience. They were also offfered a full training session on autism and understanding various techniques to support somebody who has it. A very insightful day for all involved!</div>}
                        direction="row-reverse"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="This is Me - Hariss Academy"
                        date="???"
                        image={Hariss}
                        text={<div>Team members Mandy and Keith accompanied Aston to the Thurrock based Hariss academy where Aston delivered his inspirational video, <Link to="/thisisme">This is Me</Link>. He received a great reception and the students and staff alike wwre pleased with the content and understood what we were trying to deliver. Thanks to the academy for having us!</div>}
                        direction="row"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="United for Charity Football Match"
                        date="???"
                        image={United}
                        text={<div>Chris Cooper, an avid fan of Team Avery Essex and AnnaKennedyOnline, approached us to work together on a charity football match. We were blessed with the support of Bowers footbal club to be able to put together a funday and celebrity football match to raise funds to help the homeless, run by the inspirational Sky Butcher and AnnaKennedyOnline. The day was a huge success, and both charities received an equal split of the proceeds.</div>}
                        direction="row-reverse"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="This is Me - Castledon School"
                        date="???"
                        image={Castledon}
                        text={<div>What a venue to launch Aston's inspiring film! Castledon was Aston's old school, so it seemed fitting for him to launch his new film here. He also talked to an assembly of students to answer any questions they had. Firm friendships were formed and the hope of working with Castledon moving forward. Aston, Keith and Dawn stayed on with some of the parents when the students returned to class, and gave them an insight into Astons journey. Thanks to Castledon for a great day!</div>}
                        direction="row"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="Reece's Rainbow Ball"
                        date="13th July 2019"
                        image={Reece}
                        text={<div>Team Avery Essex member and firm friend Karen Hawley sadly lost her son Reece to a brain tumour several years ago. Reece's story touched us, as well as many others, and lead to the birth of the <a href="http://www.reecehawleyfoundation.co.uk/">Reece Hawley Foundation</a>. Reece's family rundraise to send young people nearing the end of their lives to build memories with their families. The family work tirelessly for their charity, so we wanted to give them a night to reflect and raise funds to help support Reece's legacy. The event raised an incredible £5428.40 for some even more incredible people. We are priviledged to help them continue their work. Thank you for everything you do.</div>}
                        direction="row-reverse"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="Aston's Way - Police and Fire Service Training"
                        date="???"
                        image={PoliceWay}
                        text={<div>Sadly as a child Aston has a negative experience with the police, which was escalated due to a lack of understanding by the police of Aston's autism and sensory issues. After reflecting on his experience, Aston decided that more underatanding was paramount within the emergency With the support of his parents and local police inspector Steven Parry, Aston's dreams were realised. Over 1000 firefighters and police officers went through the day at Basildon Fire Station and were given the opportunity to experience the Autism Reality Bus, with Aston and his parents giving some presentations on living with autism. Aston's ultimate ain is for all new recruits to be offered this opportunity.  </div>}
                        direction="row"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="Thundersley Primary - Charity Fun Day and This is Me Presentation"
                        date="???"
                        image={Valentines}
                        text={placeholder}
                        direction="row-reverse"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="Joshua's Carriage Skydive"
                        date="5th August 2018"
                        image={Skydive}
                        text="Aston pulled off an amazing sponsored skydive to raise money for a local boy, Joshua, who has autism, dystonia, and cerebral palsy. The money went towards Joshua's Carriage, which funded an electric wheelchair and accessories for the 6 year old to have a bit more independence, and not have to rely on his mum to push him around in his manual wheelchair. Well done to Aston for doing something so brave!"
                        direction="row"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="Charity Football Fun Day with AnnaKennedyOnline"
                        date="???"
                        image={Valentines}
                        text={placeholder}
                        direction="row-reverse"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="Bowers SEN Disco"
                        date="???"
                        image={Sen}
                        text={placeholder}
                        direction="row"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="80s Night for Aston's Pamper Project"
                        date="April 2018"
                        image={Eighties}
                        text={<div>A great fundraising night for <a href="/pamper">Aston's Pamper Project</a>, with a nostalgic 80's dress theme! A great time was had by all. The money raised from this night went to helping out deserving people, treating them to a luxurious pampering. If you'd like to find out more, or even nominate someone, click the link!</div>}
                        direction="row-reverse"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        title="Austistic and Proud Fundraising Party"
                        date="September 2015"
                        image={Valentines}
                        text={placeholder}
                        direction="row"
                    />
                 </LazyLoad>
                 <LazyLoad offsetVertical={300}>
                    <Event 
                        className={classes.final}
                        title="JJ Night"
                        date="November 2013"
                        image={JJ}
                        text="JJ Night was a disco and auction to raise money for two very special local boys, Joshua and Joe. Joshua has celebral palsy, dystonia, and autism, while Joe was on dialysis. The funds raised helped to purchase specially made toys, including a custom built sandpit. It also helped take the children from the dialysis unit Joe was staying at for a day out."
                        direction="row-reverse"
                    />
                 </LazyLoad>
                 
            </Grid>
        </Grid>

    </div>
 )}
export default Events;